// 活动详情页面

<template>
  <div class="activity_detail pt">
    <DropdownBar />
    <div class="details">
        <!-- <img src="../../assets/image/activity_details.png" alt="活动详情"> -->
        <img v-show="!no_details" :src="img_url" alt="活动详情">
        <h1 v-show="no_details">无具体详情</h1>
    </div>
    <FooterBar />
  </div>
</template>

<script>
import DropdownBar from "@/components/DropdownBar";
import FooterBar from "@/components/FooterBar";
import Api from "../../api/index.js";
export default {
  name: "activity_detail",
  data() {
    return {
      img_url: "",
      no_details: false
    };
  },
  created() {
    Api.getActivityDetails(this.$route.params.id).then(res => {
      let result = res.data;
      if (result.msg_type == 0) {
        this.img_url = JSON.parse(JSON.stringify(result.data)).url;
      } else {
        this.no_details = true;
      }
    });
  },
  components: {
    DropdownBar,
    FooterBar
  }
};
</script>
<style scoped lang="less">
.activity_detail {
  background-color: #f2f2f2;
}
.details {
  overflow: hidden;
}
img {
  float: left;
}
h1 {
  font-size: 1rem;
  text-align: center;
  padding: 4rem 0;
}
</style>
